<template>
  <CRow class="justify-content-center">
    <CCol>
      <CCard>
        <CCardHeader class="d-flex justify-content-between">
          <div>
            <slot name="header">
              <CIcon name="cil-healing" />
              <strong>Customers</strong>
            </slot>
          </div>
        </CCardHeader>
        <CCardBody>
          <v-client-table
            :data="rows"
            :columns="columns"
            :options="options"
            @row-click="click($event)"
          >
            <div slot="actions" slot-scope="{row}">
              <CButton class="mr-3" variant="outline" @click="deleteRow(row)" color="danger">
                Delete
                <CIcon name="cil-delete" />
              </CButton>
            </div>
          </v-client-table>
        </CCardBody>
      </CCard>
    </CCol>
  </CRow>
</template>
<script>
export default {
  name: "Sales",
  data() {
    return {
      customer: "",
      email: "",
      phone: "",
      rows: [],
      columns: ["name", "email", "phone", "actions"],
      options: {
        columnsClasses: {
          actions: "text-center",
          view_list: "text-center",
          due_Amount: "text-center",
        },
        filterByColumn: true,
        perPage: 10,
        texts: {
          filter: "Filter:",
          filterBy: "Filter by {column}",
          count: " ",
        },
        filterable: ["name"],
        headings: {
          name: "Name",
          id: "ID",
        },
      },
    };
  },
  created() {
    this.getData()
  },
  methods: {
    click(e) {
      console.log(e);
    },
    getData() {
      this.$http
        .get("http://localhost:3000/customer/get")
        .then(res => {
          this.rows = res.data
          console.log(res.data)
        })
        .catch(err => {
          // eslint-disable-next-line no-console
          console.log(err.response)
        });
    },
    deleteRow(row) {
      this.$http
        .delete("http://localhost:3000/customer/"+row._id)
        .then(res => {
          this.getData()
        })
        .catch(err => {
          // eslint-disable-next-line no-console
          console.log(err.response)
        });
    },
  },
};
</script>